@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.container {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 960px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

/* Navbar */

.nav__wrapper {
  background: #FD3C3C;

}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
}

.nav__brand {
  font-size: 30px;
  font-weight: 700;
  color: #ddd;
}

.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 3rem;
}

.nav__link {
  color: #ddd;
}

.toggler__icon {
  cursor: pointer;
  display: none;
}

.toggler__icon div {
  width: 25px;
  height: 2px;
  background: #ddd;
  margin: 5px 35px;
  transition: 0.3s ease;
}

@media screen and (max-width: 768px) {
  .toggler__icon {
    display: block;
  }
  .nav__menu {
    position: fixed;
    background: #FA4040;
    flex-direction: column;
    top: 10vh;
    width: 40%;
    height: 90vh;
    right: 0;
    transform: translateX(100%);
    transition: 0.5s ease;
    z-index: 1;
  }
  .btn_1{
    width: 30%;
    align-content: center;
    align-items: center;
    margin: 0 0 -55px 75px;
  }
  .modal{
  width: 40%;
  margin: 0 auto;
}
.item-todo{
  max-width: 70%;
  
}
.header{
  width: 100%;
  z-index: 0;
}

}

.nav__collapse {
  transform: translateX(0%);
}

/* Toggle Icon Animation */
.toggle .line__1 {
  transform: translate(-5px, 7px) rotate(-45deg);
}
.toggle .line__2 {
  opacity: 0;
}
.toggle .line__3 {
  transform: translate(-5px, -7px) rotate(45deg);
}


  /* visual content */


  .item-todo {
    padding: 14px;
    margin: 0 0 15px 75px;
    text-align: left;
    margin-top: 15px;
    background: #FA4040;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  .item-todo{
    width: 750px;
    
    background-color: #FA4040;
  }
  .item-todo .item-text {
    font-weight: bold;
    display: inline-block;
    cursor: pointer;
  }
  .item-todo .item-delete {
    display: inline-block;
    cursor: pointer;
    float: right;
    font-size: 22px;
    position: relative;
    left: -4px;
    bottom: 5px;
  }
  .strike {
    text-decoration: line-through;
    font-weight: bold;
    color: black;
  }
  .text_content{
    text-align: center;
    margin: 30px ;
    background-color: #e0e0e0;
    width: 300px;
    border: 3px solid black;
    border-radius: 10px;
  }
  input{
    width: 260px;
    height: 35px;
  }